<template>
	<div class="alert">
		<h3>{{ x }} + {{ y }} = {{result}}</h3>
		<hr>
		<div class="buttons">
			<button class="btn btn-success" v-for="number in answers" @click="onAnswer(number)">
				{{ number }}
			</button>
		</div>
	</div>
</template>

<script>
    export default {
        name: "Question",
        data() {
            return {
                result: '?',
                x: mtRandom(100, 200),
                y: mtRandom(100, 200)
            }
        },
        computed: {
            good() {
              return this.x + this.y
			},
            answers() {
                let good = this.good;
                let res = [this.good];

                while (res.length < 4) {
                    var num = mtRandom(this.good - 20, this.good + 20);
                    if (res.indexOf(num) === -1) {
                        res.push(num)
                    }
                }

                return res.sort(function () {
                    return Math.random() > 0.5;
                });
            }
        },
        methods: {
            onAnswer(num) {
                if(num == this.good) {
                    this.$emit('success');
				} else {
                    this.$emit('error', `${this.x} + ${this.y} = ${this.good}!`);
				}
			}
		}
    }

    function mtRandom(min, max) {
        let diff = max - min;
        return Math.floor(Math.random() * (diff + 1)) + min;
    }
</script>

<style scoped>
	.btn {
		margin: 0 10px;
	}
</style>



